import React, { Suspense } from 'react';
import { Route, Routes, BrowserRouter as Router } from 'react-router-dom';

import Page from '../../page';

interface RouteObject {
    path: string,
    component: any,
}

// Lazy component imports
const AttentionDataGridGridContainer = React.lazy(() => import('../../attention-data-grid/lib/attention-data-grid-container'));
const InstanceDataGridContainer = React.lazy(() => import('../../instance-data-grid/lib/instance-data-grid-container'));
const AttentionMapContainer = React.lazy(() => import('../../attention-map/lib/attention-map-container'));

// Routes
const routes:RouteObject[] = [
    {
        path: '/attention/:id',
        component: AttentionMapContainer,
    },
    {
        path: '/attention-grid/:id',
        component: AttentionDataGridGridContainer,
    },
    {
        path: '/attention-grid',
        component: AttentionDataGridGridContainer,
    },
    {
        path: '/',
        component: InstanceDataGridContainer,
    },
    {
        path: '*',
        component: () => <p>Page not found</p>,
    },
];

export const AppRoutes = () => (
  <Router>
    <Page>
      <Routes>
        {
            routes.map((route) => {
                const Component = route.component;
                return (
                  <Route
                    path={route.path}
                    element={(
                      <Suspense fallback={<div>Loading...</div>}>
                        <Component />
                      </Suspense>
                    )}
                  />
                );
            })
        }
      </Routes>
    </Page>
  </Router>
);

export default AppRoutes;
